<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>
    <form
      class="filter__form form"
      action="#"
      method="get"
      @submit.prevent="submit"
    >
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="min-price"
            v-model.number="currentPriceMin"
          />
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input
            class="form__input"
            type="text"
            name="max-price"
            v-model.number="currentPriceMax"
          />
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select
            class="form__select"
            type="text"
            name="category"
            v-model.number="currentCategoryId"
          >
            <option value="0">Все категории</option>
            <option
              :value="category.id"
              v-for="category in categories"
              :key="category.id"
            >
              {{ category.title }}
            </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item" v-for="color in colorsData" :key="color.id">
            <label class="colors__label">
              <input
                class="colors__radio sr-only"
                type="radio"
                name="color"
                :value="color.id"
                v-model="currentColor"
                checked=""
              />
              <span
                class="colors__value"
                :style="'background-color:' + color.code"
              >
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Объемб в ГБ</legend>
        <ul class="check-list">
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="8"
                checked=""
              />
              <span class="check-list__desc">
                8
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="16"
              />
              <span class="check-list__desc">
                16
                <span>(461)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="32"
              />
              <span class="check-list__desc">
                32
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="64"
              />
              <span class="check-list__desc">
                64
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="128"
              />
              <span class="check-list__desc">
                128
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                name="volume"
                value="264"
              />
              <span class="check-list__desc">
                264
                <span>(313)</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button
        class="filter__reset button button--second"
        type="button"
        @click.prevent="reset"
      >
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import axios from "axios";
import { API_BASE_URL } from "../config";
export default {
  data() {
    return {
      currentPriceMin: 0,
      currentPriceMax: 0,
      currentCategoryId: 0,
      currentColor: 1,
      categoriesData: null,
      colorsData: null,
    };
  },
  props: ["priceMin", "priceMax", "categoryId", "page", "color"],
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
  },
  watch: {
    color(value) {
      this.currentColor = value;
    },
    priceMin(value) {
      this.currentPriceMin = value;
    },
    priceMax(value) {
      this.currentPriceMax = value;
    },
    currentId(value) {
      this.currentCategoryId = value;
    },
  },
  methods: {
    submit() {
      this.$emit("update:priceMin", this.currentPriceMin);
      this.$emit("update:priceMax", this.currentPriceMax);
      this.$emit("update:categoryId", this.currentCategoryId);
      this.$emit("update:color", this.currentColor);
      this.$emit("update:page", 1);
    },
    reset() {
      this.$emit("update:priceMin", 0);
      this.$emit("update:priceMax", 0);
      this.$emit("update:categoryId", 0);
      this.$emit("update:color", 0);
    },
    loadCategories() {
      axios
        .get(`${API_BASE_URL}/api/productCategories`)
        .then((res) => (this.categoriesData = res.data));
    },
    loadColors() {
      axios.get(`${API_BASE_URL}/api/colors`).then((res) => {
        this.colorsData = res.data.items;
        this.colorsData.splice(this.colorsData.length - 1, 1);
      });
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
  },
};
</script>
