<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="item.product.img"
        width="120"
        height="120"
        :alt="item.product.title"
      />
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <p class="product__info">Объем: <span>128GB</span></p>
    <span class="product__code"> Артикул: 1501230 </span>

    <div class="product__counter form__counter">
      <button
        type="button"
        aria-label="Убрать один товар"
        @click="decreaseAmount(item.productId)"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count" />

      <button
        type="button"
        aria-label="Добавить один товар"
        @click="increaseAmount(item.productId)"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (item.product.price * item.amount).toLocaleString("ru") }} ₽
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(item.productId)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import store from "../store";
import { mapActions, mapMutations } from "vuex";

export default {
  props: ["item"],
  methods: {
    ...mapActions({ deleteProduct: "deleteCartProduct" }),
    increaseAmount(id) {
      store.dispatch("updateCartProductAmount", {
        productId: this.item.productId,
        amount: this.item.amount + 1,
      });
    },
    decreaseAmount(id) {
      if (this.item.amount != 1) {
        store.dispatch("updateCartProductAmount", {
          productId: this.item.productId,
          amount: this.item.amount - 1,
        });
      }
    },
  },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        store.dispatch("updateCartProductAmount", {
          productId: this.item.productId,
          amount: value,
        });
      },
    },
  },
};
</script>

