<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ cartProducts.length }} товаров
      </span>
    </div>

    <section class="cart">
      <form action="#" class="cart__form form" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <base-form-text v-model="formData.name" :error="formError.name" placeholder="Введите ваше полное имя" title="ФИО"/>
            <base-form-text v-model="formData.address" :error="formError.address" placeholder="Введите ваш адрес" title="Адрес доставки"/>
            <base-form-text v-model="formData.phone" :error="formError.phone" placeholder="Введите ваш телефон" title="Телефон" type="tel"/>
            <base-form-text v-model="formData.email" :error="formError.email" placeholder="Введи ваш Email" title="Email"/>

            <base-form-textarea v-model="formData.comment" :error="formError.comment" placeholder="Ваши пожелания" title="Комментарий к заказу"/>

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input checked="" class="options__radio sr-only" name="delivery" type="radio" value="0">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" name="delivery" type="radio" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" name="pay" type="radio" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" name="pay" type="radio" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li v-for="(item, index) in cartProducts" class="cart__order">
              <h3>{{ item.product.title }}</h3>
              <b>{{ item.product.price.toLocaleString('ru') }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ cartProducts.length }}</b> товара на сумму <b>{{ (cartSum + 500).toLocaleString('ru') }} ₽</b></p>
          </div>

          <button :disabled="orderSending" class="cart__button button button--primery" type="submit">
            {{ orderSending ? 'Оформление заказа...' : 'Оформить заказ' }}
          </button>
        </div>
        <div v-if="formErrorMessage" class="cart__error form__error-block">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "@/components/BaseFormText.vue";
import BaseFormTextarea from "@/components/BaseFormTextarea.vue";
import {mapGetters} from "vuex";
import axios from "axios";
import {API_BASE_URL} from "@/config";

export default {
  name: "OrderPage",
  components: {BaseFormTextarea, BaseFormText},
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      orderSending: false,
    }
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.orderSending = true;

      setTimeout(() => {
        axios.post(API_BASE_URL + '/api/orders', {
          ...this.formData
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey
          }
        }).then((res) => {
          this.$store.commit('resetCart')
          this.$store.commit('updateOrderInfo', res.data);
          this.$router.push({name: 'orderInfo', params: {id: res.data.id}})
          this.orderSending = false;
        }).catch((error) => {
          this.formErrorMessage = error.response.data.error.message;
          this.formError = error.response.data.error.request || {};
          this.orderSending = false;
        })
      }, 2000)
    }
  },
  computed: {
    ...mapGetters(['cartProducts', 'cartSum']),
  }
}
</script>

