<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> 152 товара </span>
    </div>

    <div class="content__catalog">
      <product-filter
        :page.sync="page"
        :price-min.sync="filterPriceMin"
        :price-max.sync="filterPriceMax"
        :category-id.sync="filterCategoryId"
        :color.sync="filterColor"
      />

      <section class="catalog">
        <product-list
          :products="products"
          v-if="productsLoaded && !loadingFail"
        />

        <div v-if="!productsLoaded">Загрузка товаров...</div>
        <div v-if="loadingFail">
          Ошибка при загрузке товаров.
          <button @click.prevent="loadProducts">Повторить еще раз</button>
        </div>

        <base-pagination
          v-model="page"
          :count="productsQuantity"
          :per-page="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import axios from "axios";
import { API_BASE_URL } from "../config";

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceMin: 0,
      filterPriceMax: 0,
      filterCategoryId: 0,
      filterColor: "#73b6ea",

      page: 1,
      productsPerPage: 6,
      productsData: null,

      productsLoaded: false,
      loadingFail: false,
    };
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => {
            return { ...product, img: product.image.file.url };
          })
        : [];
    },
    productsQuantity() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceMin() {
      this.loadProducts();
    },
    filterPriceMax() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
  },
  methods: {
    loadProducts() {
      clearTimeout(this.loadTimer);
      this.loadTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceMin,
              maxPrice: this.filterPriceMax,
              colorId: this.filterColor,
            },
          })
          .then((res) => {
            this.productsData = res.data;
            this.productsLoaded = true;
          })
          .catch(() => {
            this.productsLoaded = true;
            this.loadingFail = true;
          });
      }, 0);
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
