import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import {API_BASE_URL} from "../config";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        cartProducts: [],
        userAccessKey: null,
        cartProductsData: [],
        orderInfo: null,
    },
    getters: {
        cartProducts(state) {
            return state.cartProducts.map((item) => {
                const product = state.cartProductsData.find((p) => p.product.id == item.productId).product;
                return {
                    ...item,
                    product: {
                        ...product,
                        img: product.image.file.url,
                    },
                };
            });
        },
        cartSum(state) {
            let sum = 0;
            state.cartProducts.map((i) => {
                let productItem = state.cartProductsData.find((p) => p.product.id == i.productId);
                sum = sum + i.amount * productItem.price;
            });
            return sum;
        },
        orderSum(state) {
            if (!state.orderInfo) return null
            let sum = 0;
            state.orderInfo.basket.items.map(i => {
                sum += i.price;
            })
            return sum;
        },
        userAccessKey(state) {
            return state.userAccessKey;
        },
    },
    mutations: {
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo
        },
        resetCart(state) {
            state.cartProducts = [];
            state.cartProductsData = [];
        },
        changeAmount(state, {productId, amount}) {
            const item = state.cartProducts.find((item) => item.productId == productId);
            item.amount = amount;
        },
        updateCartProductAmount(state, {productId, amount}) {
            const item = state.cartProducts.find((item) => item.productId == productId);
            if (item) {
                item.amount = amount;
            }
        },
        deleteCartProduct(state, productId) {
            state.cartProducts = state.cartProducts.filter((item) => item.productId != productId);
        },
        updateUserAccessKey(state, accessKey) {
            state.userAccessKey = accessKey;
        },
        updateCartProductsData(state, products) {
            state.cartProductsData = products;
        },
        syncCartProducts(state) {
            state.cartProducts = state.cartProductsData.map((item) => {
                return {
                    productId: item.product.id,
                    amount: item.quantity,
                };
            });
        },
    },
    actions: {
        loadOrderInfo(context, orderId) {
            return axios.get(API_BASE_URL + '/api/orders/' + orderId, {
                params: {
                    userAccessKey: context.state.userAccessKey
                }
            }).then((res) => {
                context.commit('updateOrderInfo', res.data)
            })
        },
        loadCart(context) {
            return axios
                .get(`${API_BASE_URL}/api/baskets`, {
                    params: {
                        userAccessKey: this.state.userAccessKey,
                    },
                })
                .then((res) => {
                    if (!context.state.userAccessKey) {
                        context.commit("updateUserAccessKey", res.data.user.accessKey);
                        localStorage.setItem("userAccessKey", res.data.user.accessKey);
                    }
                    context.commit("updateCartProductsData", res.data.items);
                    context.commit("syncCartProducts");
                });
        },
        addProductToCart(context, {productId, amount}) {
            return axios
                .post(
                    `${API_BASE_URL}/api/baskets/products`,
                    {
                        productId: productId,
                        quantity: amount,
                    },
                    {
                        params: {
                            userAccessKey: context.state.userAccessKey,
                        },
                    }
                )
                .then((res) => {
                    context.commit("updateCartProductsData", res.data.items);
                    context.commit("syncCartProducts");
                });
        },
        updateCartProductAmount(context, {productId, amount}) {
            context.commit("updateCartProductAmount", {productId, amount});
            if (amount < 1) return;

            return axios
                .put(
                    `${API_BASE_URL}/api/baskets/products`,
                    {
                        productId: productId,
                        quantity: amount,
                    },
                    {
                        params: {
                            userAccessKey: context.state.userAccessKey,
                        },
                    }
                )
                .then((res) => {
                    context.commit("updateCartProductsData", res.data.items);
                })
                .catch(() => {
                    context.commit("syncCartProducts");
                });
        },
        deleteCartProduct(context, productId) {
            if (confirm("Вы уверены что хотите удалить продукт из корзины?")) {
                return axios
                    .delete(`${API_BASE_URL}/api/baskets/products`, {
                        data: {
                            productId: productId,
                        },
                        params: {
                            userAccessKey: context.state.userAccessKey,
                        },
                    })
                    .then((res) => {
                        context.commit("updateCartProductsData", res.data.items);
                        context.commit("syncCartProducts");
                    });
            }
        },
    },
    modules: {},
});
